.MainCell {
  position: relative;
  height: 40px;
  width: 120px;
}

.MainCell-ErrorCounter {
  position: absolute;
  left: 0px; top: 0px;
  margin: 0px; padding: 0px;
  width: 40px; height: 40px;
  line-height: 40px;
  text-align: center;
  vertical-align: center;
  font-size: 17px;
  letter-spacing: 1px;

  color: white;
  background-color: #ffa08f;
  border-radius: 20px 20px 20px 20px;
  -moz-border-radius: 20px 20px 20px 20px;
  -webkit-border-radius: 20px 20px 20px 20px;
  border: 1px solid #ff614d;
  font-weight: bold;
  font-family: Bungee, Verdana, Helvetica, Tahoma, Sans-Serif;
  text-shadow: 0px 0px 1px black;
}

.MainCell-OK {
  position: absolute;
  left: 0px; top: 0px;
  margin: 0px; padding: 0px;
  width: 40px; height: 40px;
  line-height: 40px;
  text-align: center;
  vertical-align: center;
  font-size: 17px;
  letter-spacing: 0px;

  color: forestgreen;
  background-color: white;
  border-radius: 20px 20px 20px 20px;
  -moz-border-radius: 20px 20px 20px 20px;
  -webkit-border-radius: 20px 20px 20px 20px;
  border: 1px solid greenyellow;
  font-weight: normal;
  font-family: Bungee, Verdana, Helvetica, Tahoma, Sans-Serif;
  /*text-shadow: 0px 0px 1px black;*/
}

.MainCell-Date {
  position: absolute;
  left: 41px; top: 0px;
  margin: 0px;
  padding: 0px 0px 0px 4px;
  width: 80px; height: 20px;
  line-height: 20px;
  text-align: left;
  vertical-align: bottom;
  font-size: 12px;
  letter-spacing: 0px;
  
  /*border-bottom: 1px dotted lightcoral;*/

  white-space: nowrap;
  overflow: hidden;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";

}

.BootOk .MainCell-Date {
  border-bottom: 1px dotted greenyellow !important;
}

.BootError .MainCell-Date {
  border-bottom: 1px dotted lightcoral !important;
}


.MainCell-Time {
  position: absolute;
  left: 41px; top: 20px;
  margin: 0px;
  padding: 0px 0px 0px 4px;
  width: 80px; height: 20px;
  line-height: 20px;
  text-align: left;
  vertical-align: top;
  font-size: 12px;
  letter-spacing: 0px;
  font-wight: bold;

  white-space: nowrap;
  overflow: hidden;
  
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";

}

.BootList .jqx-grid-cell { 
  padding: 0px; border: none;
  cursor: pointer;
}

.BootList .jqx-widget-content, .BootList .jqx-reset, .BootList  { 
  border: none;
}

.chooseBootAt button { 
  border: none !important; 
}

.chooseBootAt button:active {
  border: none !important;
}


/* EVENTS */

.InfoEvent {
  color: darkgray;
}

.TroubleEvent {
  color: darkred;
}

.EventIconWrapper {
  display: inline-block;
  width: 18px;
  font-size: 11px;
  vertical-align: middle;
  line-height: 11px;
  margin-top: -3px;
  /*border: 1px solid green*/
}

.Event {
  margin-bottom: 3px;
  padding-bottom: 3px;
  list-style-type: none;
  text-indent: -11px;
  padding-left: 0px;
  border-bottom: 1px solid lightgrey;
  margin-left: 35px;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 14px;
}

.Event:last-child {
  margin-bottom: 3px;
  padding-bottom: 3px;
  border-bottom: none;
}

.Event u {
  text-decoration: none;
  border-bottom: 1px dotted red
}

#EventAt { 
  text-align: left; 
  /*min-width: 10px;*/
  /*width: 10px;*/
  display: inline-block;
}

.Hidden { display: none}


